@import url("https://fonts.googleapis.com/css2?family=Tenor+Sans&display=swap");
.App {
  padding: 0;
  margin: 0;
  background-color: rgb(29, 29, 29);
  color: white;
  font-family: "Tenor Sans", sans-serif;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.App > h2 {
  font-size: 72px;
  background: -webkit-linear-gradient(#eee, #333);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

/* Navbar Css Area  */
.navbar_css {
  border: 1px solid yellow;
  position: sticky;
  top: 0;
  left: 0;
}
.navbar_css > div {
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
.navbar_css > div > div:nth-child(1) {
  /* border: 1px solid black; */
  font-size: 26px;
  font-weight: 600;
  letter-spacing: 6px;
  width: 200px;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.navbar_css > div > div:nth-child(1) > img {
  animation: 2s spinBulb infinite;
  overflow: hidden;
  border-radius: 50%;
}
.navbar_css > div > div:nth-child(1) > img:hover {
  border:1px solid linear-gradient(180deg, red, yellow);
  cursor: pointer;
 
}

.navbar_css > div > div:nth-child(2) {
  /* border: 2px solid black; */
  justify-content: space-between;
  border-radius: 5px;
  transition: all 1s ease-in-out;
}
.navbar_css > div > div:nth-child(2) > div > button{
  font-weight: 600;
  /* border: 1px solid blue; */

}

.navbar_css > div > div:nth-child(2) > div {
  padding: 5px 15px;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  outline: none;
  color: #fff;
  background: rgb(29, 29, 29);
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 5px;
}
.navbar_css > div > div:nth-child(2) > div:before {
  content: "";
  background: linear-gradient(
    45deg,
    #ff0000,
    #ff7300,
    #fffb00,
    #48ff00,
    #00ffd5,
    #002bff,
    #7a00ff,
    #ff00c8,
    #ff0000
  );
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(1px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing 20s linear infinite;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  border-radius: 5px;
}
.navbar_css > div > div:nth-child(2) > div:active {
  color: #000;
}
.navbar_css > div > div:nth-child(2) > div:active:after {
  background: transparent;
}
.navbar_css > div > div:nth-child(2) > div:hover:before {
  opacity: 1;
}

.navbar_css > div > div:nth-child(2) > div:after {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgb(29, 29, 29);
  left: 0;
  top: 0;
  border-radius: 5px;
}

/* Home Css Area  */
.home_css {
  border: 1px solid blue;
  max-width: 95%;
  margin: auto;
  height: 6000px;
}

@keyframes glowing {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}

@media only screen and (max-width: 800px) {
}

@keyframes spinBulb {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
